import React from 'react'
import { Row, Col } from 'antd'
import { PlusOutlined, MinusOutlined } from '@ant-design/icons'
import { RouteComponentProps } from 'react-router-dom'
import {
  pathOr,
  keys,
  filter,
  map,
  mergeAll,
  omit,
  sort,
  last as Rlast,
  inc,
} from 'ramda'
import styled from 'styled-components'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import Input from '@/components/input'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import {
  useSnippetCategory,
  useReportSnippets,
  useReportPreview,
} from '@/hooks/useReportTemplate'
import { REPORT_TEMPLATE_LIST } from '@/constants/routes'
import { confirm } from '@/utils/message'
import { getNumberFromString } from '@/utils/webHelper'
import ReactDndProvider from '@/components/snippet-scrollable-container'
import SnippetListMenu from '@/components/snippet-list-menu'
import SnippetDragLayer from '@/components/snippet-drag-layer'
import SnippetsContainer from '@/components/snippets-container'
import SnippetPreviewModal from '@/components/snippet-preview-modal'
import { Collapse, Tabs } from './components'
const { compose } = require('ramda')

interface Props extends RouteComponentProps<{ id: string }> {}

function ReportTemplateBuilder(props: Props) {
  useTitle('結構式報告模版編輯')

  useBreadcrumb([
    { href: REPORT_TEMPLATE_LIST, name: '結構式報告' },
    { name: '結構式報告模版編輯' },
  ])

  const { params } = props.match

  const [category, setCategory] = useSnippetCategory()
  const [snippetList, setSnippetList] = useReportSnippets(params.id)

  const [, setTemplateInfo] = React.useState({})

  // React.useEffect(() => {
  //   console.log(
  //     JSON.stringify(
  //       compose(
  //         mergeAll,
  //         map((key: string) => ({
  //           [key]: compose(
  //             map(omit(['Component'])),
  //             pathOr([], [key])
  //           )(snippetList),
  //         })),
  //         keys
  //       )(snippetList)
  //     )
  //   )
  // }, [JSON.stringify(snippetList)]) // eslint-disable-line

  const [isPreview, handlePreview] = useReportPreview()

  const handleSave = () => {}

  useHeaderContent(
    <div className="inline-flex">
      <Button
        color="outline"
        className="bg-transparent text-white border-white">
        發佈
      </Button>
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handleSave}>
        儲存
      </Button>
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handlePreview}>
        預覽
      </Button>
    </div>
  )

  const handleInputChange = (field: string) => (
    evt: React.ChangeEvent<HTMLInputElement>
  ) => {
    setTemplateInfo(prev => ({ ...prev, [field]: evt.target.value }))
  }

  const handleUpdate = (data: any[]) => {
    setSnippetList(prev => ({ ...prev, [category]: data }))
  }

  const handleTabChange = (activeKey: string) => {
    setCategory(activeKey)
  }

  const handleEdit = (
    e:
      | string
      | React.MouseEvent<Element, MouseEvent>
      | React.KeyboardEvent<Element>,
    action: 'add' | 'remove'
  ) => {
    const handler = action === 'add' ? handleTabAdd : handleTabRemove

    handler(e as string)
  }

  const handleTabAdd = () => {
    const last = compose(
      inc,
      pathOr(0, []),
      getNumberFromString,
      Rlast,
      sort((a: any, b: any) => a - b),
      keys
    )(snippetList)

    setSnippetList(prev => ({ ...prev, [`新標籤頁${last}`]: [] }))
    setCategory(`新標籤頁${last}`)
  }

  const handleTabRemove = (targetKey: string) => {
    confirm({
      title: '確認要刪除此頁籤嗎？',
      content: '刪除會連同模版資料一起刪除，確認繼續？',
      onOk: () => {
        setSnippetList(prev => omit([targetKey], prev))

        if (category === targetKey) {
          setCategory(
            compose(
              Rlast,
              filter((key: string) => key !== targetKey),
              keys
            )(snippetList)
          )
        }
      },
    })
  }

  const handleTabNameChange = (targetKey: string) => (
    evt: React.FocusEvent<HTMLSpanElement>
  ) => {
    const newTabName = evt.target.innerHTML

    setSnippetList(prev =>
      compose(
        mergeAll,
        map((key: string) => ({
          [key === targetKey ? newTabName : key]: prev[key],
        })),
        keys
      )(prev)
    )
    setCategory(newTabName)
  }

  return (
    <ReactDndProvider>
      {isPreview && (
        <SnippetPreviewModal
          snippetList={snippetList}
          onCancel={handlePreview}
        />
      )}
      <SnippetDragLayer />
      <Page>
        <RowM gutter={48} className="pt-2">
          <Col span={5}>
            <Collapse
              defaultActiveKey={[1, 2]}
              expandIconPosition="right"
              className="shadow-2xl"
              expandIcon={({ isActive }) =>
                isActive ? <MinusOutlined /> : <PlusOutlined />
              }>
              <Collapse.Panel header="模版資訊" key={1}>
                <Row className="mb-2 text-sm text-primary">
                  <Col span={24} className="mb-3">
                    <div className="inline-block" css={{ width: 60 }}>
                      名稱:
                    </div>
                    <Input
                      className="w-2/3"
                      onChange={handleInputChange('name')}
                    />
                  </Col>
                  <Col span={24} className="my-3">
                    <div className="inline-block" css={{ width: 60 }}>
                      描述:
                    </div>
                    <Input
                      className="w-2/3"
                      onChange={handleInputChange('description')}
                    />
                  </Col>
                  <Col span={24} className="my-3">
                    <div className="inline-block" css={{ width: 60 }}>
                      創作者:
                    </div>
                    <Input
                      className="w-2/3"
                      onChange={handleInputChange('author')}
                    />
                  </Col>
                </Row>
              </Collapse.Panel>
              <Collapse.Panel header="工具列" key={2}>
                <Row>
                  <Col span={24}>
                    <SnippetListMenu className="text-primary" />
                  </Col>
                </Row>
              </Collapse.Panel>
            </Collapse>
          </Col>
          <Tabs
            type="editable-card"
            activeKey={category}
            onChange={handleTabChange}
            onEdit={handleEdit}
            className="flex-1">
            {compose(
              map((key: string) => (
                <Tabs.TabPane
                  closable
                  key={key}
                  tab={
                    <span
                      contentEditable
                      suppressContentEditableWarning
                      spellCheck={false}
                      className="outline-none"
                      css={{ whiteSpace: 'nowrap' }}
                      onChange={handleTabNameChange(key)}
                      // onBlur={handleTabNameChange(key)}
                    >
                      {key}
                    </span>
                  }>
                  <Col
                    span={19}
                    className="report-template-builder__main-content">
                    <SnippetsContainer
                      draggable={!isPreview}
                      snippetList={snippetList[key]}
                      onSnippetUpdate={handleUpdate}
                    />
                  </Col>
                </Tabs.TabPane>
              )),
              keys
            )(snippetList)}
          </Tabs>
        </RowM>
      </Page>
    </ReactDndProvider>
  )
}

const RowM = styled(Row)`
  position: relative;

  .report-template-builder {
    &__main-content {
      min-height: 371.25mm;
      margin: 0 auto;
      padding: 16px;
      box-sizing: border-box;
      max-width: 100%;
      background: #fff;
      border-top-right-radius: 16px;
      border-bottom-left-radius: 16px;
      border-bottom-right-radius: 16px;
    }

    &__contentarea {
      min-height: 348.75mm;
    }

    &__contentarea--fixed-layout {
      min-height: auto;
    }

    &__snippet-toolbar {
      visibility: hidden;
    }
  }

  .ui-draggable {
    &:focus,
    &:active {
      outline: 0;

      .course-builder__snippet-toolbar {
        visibility: visible;
        opacity: 1;
        transition: visibility 0s, opacity 0.3s, max-height 0.6s linear;
      }
    }
  }
`

export default ReportTemplateBuilder
