import React from 'react'
import styled from 'styled-components'
import cx from 'classnames'
import withScroll, { createVerticalStrength } from './withScroll'

interface Props {
  className?: string
  useScroll?: boolean
}

const DndScrollableElement = styled.div`
  height: 100vh;
  padding-top: 0;
  overflow-y: scroll;

  &.is--preview {
    height: auto;
  }
`

const ScrollableComponent = withScroll(DndScrollableElement)

const vStrength = createVerticalStrength(150)

export default class ReactDndProvider extends React.Component<Props> {
  static defaultProps = {
    useScroll: true,
  }

  render() {
    const { className, useScroll } = this.props

    return (
      <ScrollableComponent
        useScroll={useScroll}
        verticalStrength={vStrength}
        strengthMultiplier={100}
        className={cx('report-template-builder', className)}>
        {this.props.children}
      </ScrollableComponent>
    )
  }
}
